import React, { useState } from 'react';

const YooKassaWidget = () => {
  const [paymentId, setPaymentId] = useState(''); //    ID  

  const createPayment = async () => {
    try {
      const response = await fetch('https://api.yookassa.ru/v3/payments', {
        method: 'POST',
        headers: {
          'Authorization': 'Bearer test_vD-fSqrRKQl_Gkwdld7Ih7bUhj_7BBYYIrMn5PXsH_A',
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          amount: {
            value: '100.00',
            currency: 'RUB',
          },
          description: ' ',
          confirmation: {
            type: 'redirect',
            return_url: 'https://www.youtube.com',
          },
        }),
      });
      const data = await response.json();
      setPaymentId(data.id); //  ID  
    } catch (error) {
      console.error('  :', error);
    }
  };

  return (
    <div>
      <button onClick={createPayment}> </button>
      {paymentId && <p>ID  : {paymentId}</p>}
    </div>
  );
};

export default YooKassaWidget;
